<!-- 自定义头部2 首页 -->
<template>
	<view class="headtwo" :style="{'padding-top': topheight +'px'}">
		<!--#ifdef  H5 || APP-PLUS -->
		<view class="headbox">
			<image class="img" src="../../../static/img/mlogo.png" mode="aspectFit"></image>
		</view>
		<!-- #endif -->
		<view class="capsule" @click="headbut">
			<image class="img" src="../../../static/img/fangdajing.png"></image>
			<view class="capsule-text">
				<swiper class="textbox" vertical="true" duration="500" interval="3000" circular="true" autoplay>
					<swiper-item  v-for="(item,index) in resou" :key="index">
						<view> {{item.name}} </view>
					</swiper-item>
				</swiper>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name:"headtwo",
		data() {
			return {
				topheight:0,//距离顶部的高
			}
		},
		props:{
			resou:{
				type:Array,
				default:() => [],
			}
		},
		//组件创建完毕执行
		created(){
			this.topheight = uni.getSystemInfoSync().statusBarHeight+5;//顶部距离
		},
		headbut(){//输出头部点击事件
			this.$emit('headbut');
		}
	}
</script>

<style lang="scss" scoped>
.headtwo{
	position: fixed;
	top: 0px;
	left: 0px;
	z-index: 10;
	width: 100%;
	height: 35px;
	line-height: 35px;
	background: $uni-color-color;
	display: flex;
	justify-content: space-between;
}
.headbox{
	width: 75px;
	height: 30px;
	margin: 0px 10px;
}
.headbox .img{
	width: 75px;
	height: 30px;
}
.capsule{
	width: 65%;
	height: 30px;
	margin: 0px 10px;
	border-radius: 15px;
	background: #fff;
	overflow: hidden;
	display: flex;
}
.capsule .img{
	width: 20px;
	height: 20px;
	margin: 5px;
}
.capsule-text{
	flex: 1;
	height:30px;
	font-size: $uni-name-font;
	color: #666;
	overflow: hidden;
}
.textbox{
	width: 100%;
	height: 30px;
	line-height: 30px;
}
</style>
